import { createSlice } from "@reduxjs/toolkit";

//usestate  // state  slice 
let user = createSlice({
    name: "user",
    // initialState: "daseul",

    initialState: {
        name: "daseul",
        age: 21,
    },

    //
    reducers: {
        changeName(state) {
            // state
            state.name = `jeong ${state.name}`;

            // return `jeong ${state}`;
        },
        changeNum(state) {
            state.age += 1;
        },
    },
});
export let { changeName, changeNum } = user.actions;
export default user;
